import React, { useState } from 'react';

const App = () => {
  const [result, setResult] = useState('');
  const handleClick = (e) => {
    setResult(result.concat(e.target.name));
  };

  const clear = () => {
    setResult('');
  };
  const backspace = () => {
    setResult(result.slice(0, -1));
  };
  const calculate = () => {
    try {
      setResult(eval(result).toString());
    } catch (err) {
      setResult('Error');
    }
  };
  return (
    <>
      <div className="container calculator">
        <div className="calculator__title">
          <span>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="-11.5 -10.23174 23 20.46348"
            >
              <title>React Logo</title>
              <circle cx="0" cy="0" r="2.05" fill="white" />
              <g stroke="white" stroke-width="1" fill="none">
                <ellipse rx="11" ry="4.2" />
                <ellipse rx="11" ry="4.2" transform="rotate(60)" />
                <ellipse rx="11" ry="4.2" transform="rotate(120)" />
              </g>
            </svg>
          </span>
          <h4>Simple React Calculator</h4>
        </div>
        <form className="display">
          <input
            className="display__input"
            type="text"
            value={result}
            disabled
          />
        </form>
        <div className="keypad">
          <button className="highlight highlight--2" onClick={clear} id="clear">
            Clear
          </button>
          <button
            className="highlight highlight--2"
            name=""
            onClick={backspace}
            id="backspace"
          >
            C
          </button>
          <button className="highlight highlight--1" name="/" onClick={handleClick}>
            &divide;
          </button>
          <button name="7" onClick={handleClick}>
            7
          </button>
          <button name="8" onClick={handleClick}>
            8
          </button>
          <button name="9" onClick={handleClick}>
            9
          </button>
          <button className="highlight highlight--1" name="*" onClick={handleClick}>
            &times;
          </button>
          <button name="4" onClick={handleClick}>
            4
          </button>
          <button name="5" onClick={handleClick}>
            5
          </button>
          <button name="6" onClick={handleClick}>
            6
          </button>
          <button className="highlight highlight--1" name="-" onClick={handleClick}>
            &ndash;
          </button>
          <button name="1" onClick={handleClick}>
            1
          </button>
          <button name="2" onClick={handleClick}>
            2
          </button>
          <button name="3" onClick={handleClick}>
            3
          </button>
          <button className="highlight highlight--1" name="+" onClick={handleClick}>
            +
          </button>
          <button name="0" onClick={handleClick}>
            0
          </button>
          <button className="highlight highlight--1" name="." onClick={handleClick}>
            .
          </button>
          <button className="highlight highlight--1" onClick={calculate} id="result">
            =
          </button>
        </div>
      </div>
    </>
  );
};

export default App;
